export default {
	data() {
		return {
			id: 0,
			detail: {}
		}
	},
	methods: {
		// 获取详情页数据
		async getNewsDetail() {
			const res = await this.$myRequest({
				url: '/api/getnew/' + this.id
			})
			this.detail = res.data.message[0]
		}
	},
	// 可以从options中获得id
	onLoad(options) {
		this.id = options.id
		this.getNewsDetail()
	},
}
